import React from 'react';
import Gallery from './data.jsx';
import User from './UserComponent.jsx';

class Page extends React.Component {

constructor(props){
        super(props);
        this.state = {
          username : '',
          useravatar:'',
          url:'',
          tag:'',
          data: []
         

        }
        this.handleUserName = this.handleUserName.bind(this);
this.handlePost = this.handlePost.bind(this);
  this.createPost = this.createPost.bind(this);
        
    }

    handlePost(url, tag){
     
       this.setState({url: url,tag:tag}); 


    }

  handleUserName(name) {

    this.setState({username: name}); 
   }

createPost(){
  console.log("here!");
  var url = this.props.postURL;
var tag = this.props.postTag;
var username = this.props.username;

console.log(" url>>  "+url + " tag>> "+ tag+" name>> "+username);
  this.state.data.push({
  im:{url},
  postbyname:"h",
  postbyavatar:"https://pbs.twimg.com/profile_images/378800000483764274/ebce94fb34c055f3dc238627a576d251.jpeg",
  ti:"New Post",
  hashTag:{tag},
  comments:
    [
      
    ]
});
} 
  
render(){


 //console.log("final "+this.state.url);
return (<div>
   <User username={this.handleUserName} post = {this.handlePost}/>
 
  
    <Gallery username={this.state.username} createPost = {this.handlePost} postURL = {this.state.url} postTag = {this.state.tag}/>
   

</div>);

}

}


export default Page;


